<template>
  <v-card class="mx-auto" max-width="400" >
    <v-card-title class="subtitle-1">Sum Of Screening Step</v-card-title>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        icon
        @click="calculateEff()"
      >
      <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
      </v-btn>
    </v-card-actions>

    <v-expand-transition>
      <div v-show="show">
      <v-card-text class="text--primary">
        <span> 1. Step </span>
        <v-flex >
        <span style="color:orange"> Solid : {{$store.getters.solid}} </span>
        </v-flex>
        <v-flex>
        <span style="color:orange"> Water : {{$store.getters.water}} </span>
        </v-flex>
      </v-card-text>

      <v-card-text class="text--primary">
        <span> 2. Step </span>
        <v-flex >
        <span style="color:orange"> Solid : {{$store.getters.elekust_solid}} </span>
        </v-flex>
        <v-flex>
        <span style="color:orange"> Water : {{$store.getters.elekust_water_moist}} </span>
        </v-flex>
      </v-card-text>    

      <v-card-text class="text--primary">
        <span> 3. Step </span>
        <v-flex >
        <span style="color:orange"> Solid : {{$store.getters.eleksagalt_solid}} </span>
        </v-flex>
        <v-flex>
        <span style="color:orange"> Water : {{$store.getters.eleksagalt_water_moist}} </span>
        </v-flex>
      </v-card-text>    

      <v-card-text class="text--primary">
        <span> 4. Step </span>
        <v-flex >
        <span style="color:orange"> Solid : {{$store.getters.elekcikis_solid}} </span>
        </v-flex>
        <v-flex>
        <span style="color:orange"> Water : {{$store.getters.elekcikis_waterSR}} </span>
        </v-flex>
      </v-card-text>   

      <v-card-text class="text--primary">
        <span> 5. Step </span>
        <v-flex >
        <span style="color:orange"> Scr. Eff 1 : {{eff1}} </span>
        </v-flex>
        <v-flex>
        <span style="color:orange"> Scr. Eff 2 : {{eff2}} </span>
        </v-flex>
      </v-card-text> 
      </div>
    </v-expand-transition>      
  </v-card>
</template>

<script>
  export default {
    methods: {
      calculateEff() {
        let newValue;
        newValue = (((this.$store.getters.besleme_f - this.$store.getters.elekust_underScreen_U) / (this.$store.getters.elekust_o - this.$store.getters.elekust_underScreen_U)) * ((this.$store.getters.elekust_o - this.$store.getters.besleme_f) / (this.$store.getters.elekust_o - this.$store.getters.elekust_underScreen_U)) * ((100 - this.$store.getters.elekust_o) / (100 - this.$store.getters.besleme_f)) * this.$store.getters.elekust_underScreen_U / this.$store.getters.besleme_f).toFixed(2)
        this.eff1 = newValue
        this.calculateEff2()
        this.show = true
      },
      calculateEff2() {
        let newValue;
        newValue = (((this.$store.getters.elekust_underScreen_f - this.$store.getters.elekcikis_u) / (this.$store.getters.eleksagalt_o - this.$store.getters.elekcikis_u)) * ((this.$store.getters.eleksagalt_o - this.$store.getters.elekust_underScreen_f)/(this.$store.getters.eleksagalt_o - this.$store.getters.elekcikis_u)) * ((100-this.$store.getters.eleksagalt_o)/(100-this.$store.getters.elekust_underScreen_f)) * this.$store.getters.elekcikis_u / this.$store.getters.elekust_underScreen_f).toFixed(2)
        this.eff2 = newValue
      }
    },

    data() {
      return {
        show: false,
        eff1: "",
        eff2: ""
      };
    }
  };
</script>