<script>
import store from "../../store/store";

export default {
  methods: {
    getElek(event) {
      this.eleksayisi = event.target.value;
      this.eleksayisi = parseInt(this.eleksayisi);
    },
    createTableColumns() {
      this.reRender();
      this.columns = [];
      let i = 1;
      let data = "";
      let data2 = "";
      let itemData = "";
      if (this.columns.length !== this.howMuch * 2 + 2) {
        let first = { text: "TOP", value: "top" };
        let second = { text: "BOTTOM", value: "bottom" };
        this.columns.push(first);
        this.columns.push(second);

        for (i; i < parseInt(this.howMuch) + 1; i++) {
          data = { text: "SF" + [i] + "" + "M%", value: "sf" + [i] + "M" };
          data2 = { text: "SF" + [i] + "" + "A%", value: "sf" + [i] + "A" };
          this.columns.push(data);
          this.columns.push(data2);
        }
      }
      this.reRender();
      for (let i = 0; i < parseInt(this.howMuch) * 2 + 2; i++) {
        let sil = this.columns[i].value;
        itemData = itemData + sil + ":" + "''" + ",";
      }
      itemData = itemData.slice(0, -1);
      let obj = [];
      for (let j = 1; j < parseInt(this.eleksayisi) + 1; j++) {
        obj = eval("({" + itemData + "})");
        this.items.push(obj);
      }
    },
    reRender() {
      this.lastCheck += 1;
    },
    save() {
      this.snack = true;
      this.snackColor = "success";
      this.snackText = "Data saved";
      this.controlAndCreate()
    },
    cancel() {
      this.snack = true;
      this.snackColor = "error";
      this.snackText = "Canceled";
    },
    open() {
      this.snack = true;
      this.snackColor = "info";
      this.snackText = "Dialog opened";
    },
    close() {
      console.log("kapandi");
    },
    test() {
      console.log(this.items[0]["top"])
    },
    controlAndCreate() {

      for (let i=0; i < this.items.length; i++ ) {
        let controlArray = []
        let iterator = this.items[i].values()
        for (let j = 0; j < this.items[i].length; j++) {
          if (iterator.next().value.length != 0) {
            controlArray.push(this.items[i][j])
          }
        }
        if (controlArray.length === this.items[i].length) {
          console.log("yaratiyoruz.")
        } else {
          continue;
        }
      }
    }
  },
  data() {
    return {
      snack: false,
      snackColor: "",
      snackText: "",
      dialog: false,
      howMuch: 10,
      eleksayisi: "",
      columns: [],
      items: [],
      lastCheck: 0
    };
  }
};
</script>