<template>
  <v-layout row justify-center>
    <v-tooltip top>
      <template v-slot:activator="{ on }">
        <v-btn 
        depressed
        v-on="on"
        fab    
        width="30"
        class="transparent"
        absolute    
        @click.native.stop="runall()"  
        ></v-btn> 
      </template>     
      <span style="color:orange">FEED OF SECOND DMC</span>
    </v-tooltip>
    <v-dialog v-model="dialog" max-width="600px">
      <v-stepper v-model="e1">
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-divider></v-divider>
            <v-card class="mb-12"> 
              <v-card-title> Feed DMC ( 2nd ) </v-card-title>
              <v-flex pa-5> 
                <v-flex>
                    <span style="color:orange">Solid Flow rate (t/h) : {{$store.getters.firstDMCsink_Solid_Flow_Rate_th}}  </span>  <!-- Taşınmış değer -->
                </v-flex>
                <v-flex>
                    <span style="color:orange">Solid Flow rate (m3/h): {{$store.getters.firstDMCsink_Solid_Flow_Rate_m3t}} </span>  <!-- Taşınmış değer -->
                </v-flex>
                <v-flex>
                    <span style="color:orange">medium Flow rate (t/h): {{$store.getters.secondDMCfeed_Medium_Flow_Rate_th}}  </span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">medium Flow rate (m3/h): {{$store.getters.secondDMCfeed_Medium_Flow_Rate_m3h}} </span>
                </v-flex>           
                <v-flex>
                    <span style="color:orange">feed Flow rate (t/h): {{$store.getters.secondDMCfeed_Feed_Flow_Rate_th}} </span>
                </v-flex>                         
                <v-flex>
                    <span style="color:orange">feed Flow rate (m3/h): {{$store.getters.secondDMCfeed_Feed_Flow_Rate_m3h}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">Ash,%: {{$store.getters.firstDMCsink_Ash}}</span> <!-- Taşınmış Değer -->
                </v-flex>
                <v-flex>
                    <span style="color:white">Inlet pressure bar: </span>
                    <input v-model="$store.getters.secondDMCfeed_Inlet_Pressure_Bar" @input="getFeed_Inlet_Pressure_Bar" placeholder="doldur" />
                </v-flex>              
                <v-flex>
                    <span style="color:white">Coal Mass (Sample), kg: </span>
                    <input v-model="$store.getters.secondDMCfeed_Coal_Mass" @input="getFeed_Coal_Mass" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:orange">Coal density: {{$store.getters.firstDMCsink_Solid_Density}} </span> <!-- Taşınmış Değer -->
                </v-flex>
                <v-flex>
                    <span style="color:white">Coal Volume (Sample): {{$store.getters.secondDMCfeed_Coal_Volume}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:white">medium Mass (Sample), kg: </span>
                    <input v-model="$store.getters.secondDMCfeed_Medium_Mass" @input="getFeed_Medium_Mass" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:orange">medium density: {{$store.getters.firstDMCMedium_m4density}}</span>
                </v-flex>              
                <v-flex>
                    <span style="color:orange">medium Volume (Sample): {{$store.getters.secondDMCfeed_Medium_Volume}}</span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">MCR: {{$store.getters.secondDMCfeed_MCR}}</span>
                    
                </v-flex>
                <v-flex>
                    <span style="color:white">H, cm: </span>
                    <input v-model="$store.getters.secondDMCfeed_H_cm" @input="getFeed_H" placeholder="doldur" />
                </v-flex>
                <v-flex>
                        <span style="color:orange">P, inlet pressure calculated: {{$store.getters.secondDMCfeed_Inlet_Pressure_Calculated}}</span>
                </v-flex>
                <v-divider></v-divider>
                    <span style="color:white">DC2: </span>                  
                    <input v-model="$store.getters.secondDMCfeed_DC1" @input="getFeed_DC1" placeholder="doldur" />
              </v-flex>            
            </v-card>
                <div class="text-center">
                    <v-dialog
                    width="500"
                    >
                    <template v-slot:activator="{ on }">
                        <v-btn v-on="on">
                        DMC 2ND Properties
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-title> DMC 2ND Properties </v-card-title>
                        <v-flex pa-6>
                            <v-flex>
                                <span style="color:orange">SGm: {{$store.getters.firstDMCMedium_m4density}} </span> <!-- Taşınmış değer -->
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">SGum: </span>
                                <input v-model="$store.getters.secondDMCPropertiesSGum" @input="getfeed_PropertiesSgum" placeholder="doldur" />
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">SGom: </span>
                                <input v-model="$store.getters.secondDMCPropertiesSGom" @input="getfeed_PropertiesSgom" placeholder="doldur" />
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">SGum-SGom: {{$store.getters.secondDMCPropertiesSGumSGom}} </span>
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">(SGm-Sgom)/SGm: {{$store.getters.secondDMCPropertiesSGumSGom_SGm}} </span>
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">Top Size (Feed):</span>
                                <input v-model="$store.getters.secondDMCPropertiesTopSize" @input="getfeed_PropertiesTopSize" placeholder="doldur" />
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">Bottom Size (Feed): </span>
                                <input v-model="$store.getters.secondDMCPropertiesBottomSize" @input="getfeed_PropertiesBottomSize" placeholder="doldur" />
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">Dmean: {{$store.getters.secondDMCPropertiesDmean}}</span>
                            </v-flex>  
                            <v-flex>
                                <span style="color:orange">RD50: {{$store.getters.secondDMCPropertiesRD50}}</span>
                            </v-flex>                              
                        </v-flex>                                                                                                                                                                                                  
                    </v-card>
                    </v-dialog>
                </div>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  methods: {

    runall() {
        this.calFeed_Medium_Flow_Rate_m3h()
        this.dialog = true

    },
    
    getPropertiesSGm(event) {
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesSGm',
            value: event.target.value
        });
        this.calFeed_Medium_Flow_Rate_th()
    },
    
    calFeed_Medium_Flow_Rate_m3h() {
        let newvalue = (this.$store.getters.firstDMCsink_Solid_Flow_Rate_m3t * this.$store.getters.firstDMCMedium_mcr)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Medium_Flow_Rate_m3h',
            value: newvalue
        });  
        this.calFeed_Medium_Flow_Rate_th()  
        this.calFeed_Feed_Flow_Rate_th()   
        this.calFeed_Feed_Flow_Rate_m3h() 
    },
    
    calFeed_Medium_Flow_Rate_th() {
        let newvalue = (this.$store.getters.secondDMCfeed_Medium_Flow_Rate_m3h * this.$store.getters.firstDMCMedium_m4density)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Medium_Flow_Rate_th',
            value: newvalue
        });        
    },
    
    calFeed_Feed_Flow_Rate_th() {
        let newvalue = parseFloat(this.$store.getters.firstDMCsink_Solid_Flow_Rate_th + this.$store.getters.secondDMCfeed_Medium_Flow_Rate_th).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Feed_Flow_Rate_th',
            value: newvalue
        });         
    },

    calFeed_Feed_Flow_Rate_m3h() {
        let newvalue = parseFloat(parseFloat(this.$store.getters.firstDMCsink_Solid_Flow_Rate_m3t) + parseFloat(this.$store.getters.secondDMCfeed_Medium_Flow_Rate_m3h)).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Feed_Flow_Rate_m3h',
            value: newvalue
        });         
        this.calFeed_MCR()
    },
    
   
    getFeed_Inlet_Pressure_Bar(event) {
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Inlet_Pressure_Bar',
            value: event.target.value
        });        
    },

    getFeed_Coal_Mass(event) {
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Coal_Mass',
            value: event.target.value
        });        
        this.calFeed_Coal_Volume()
    },

    calFeed_Coal_Volume() {
        let newvalue = (this.$store.getters.secondDMCfeed_Coal_Mass / this.$store.getters.firstDMCsink_Solid_Density).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Coal_Volume',
            value: newvalue
        });         
    },

    getFeed_Medium_Mass(event) {
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Medium_Mass',
            value: event.target.value
        });  
        this.calFeed_MediumVolumeSample()
    },    


    calFeed_MediumVolumeSample() {
        let newvalue = (this.$store.getters.secondDMCfeed_Medium_Mass / this.$store.getters.firstDMCMedium_m4density).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Medium_Volume',
            value: newvalue
        }); 
    },
    
    calFeed_MCR() {
        let newvalue = (this.$store.getters.secondDMCfeed_Medium_Flow_Rate_m3h / this.$store.getters.firstDMCsink_Solid_Flow_Rate_m3t).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_MCR',
            value: newvalue
        });         
    },

    getFeed_H(event) {
        this.$store.commit("setX", {
            x: 'secondDMCfeed_H_cm',
            value: event.target.value
        });  
    },    

    getFeed_DC1(event) {
        this.$store.commit("setX", {
            x: 'secondDMCfeed_DC1',
            value:event.target.value
        })
        this.calFeedP_inlet_pressure()
    },

    calFeedP_inlet_pressure() {
        let newvalue = parseFloat(parseFloat(9*this.$store.getters.secondDMCfeed_DC1+this.$store.getters.secondDMCfeed_H_cm)*(this.$store.getters.firstDMCMedium_m4density * 9.81) / 10000 ).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCfeed_Inlet_Pressure_Calculated',
            value: newvalue
        });                 
    },

    getfeed_PropertiesSgum(event) {
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesSGum',
            value:event.target.value
        })
        this.calfeed_PropertiesRD50()
        this.getfeed_PropertiesSgumSgom()
    },    

    getfeed_PropertiesSgom(event) {
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesSGom',
            value:event.target.value
        })
        this.calfeed_PropertiesRD50()
        this.getfeed_PropertiesSgumSgom_Sgm()
        this.getfeed_PropertiesSgumSgom()
    }, 
    
    getfeed_PropertiesSgumSgom() {
        let newvalue = (this.$store.getters.secondDMCPropertiesSGum - this.$store.getters.secondDMCPropertiesSGom).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesSGumSGom',
            value: newvalue
        });
    }, 
    
    getfeed_PropertiesSgumSgom_Sgm() {
        let newvalue = ((this.$store.getters.firstDMCMedium_m4density - this.$store.getters.secondDMCPropertiesSGom) / this.$store.getters.firstDMCMedium_m4density).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesSGumSGom_SGm',
            value: newvalue
        });
    }, 
    
    getfeed_PropertiesTopSize(event) {
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesTopSize',
            value:event.target.value
        });

        this.calfeed_PropertiesDmean()
    }, 
    
    getfeed_PropertiesBottomSize(event) {
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesBottomSize',
            value:event.target.value
        });
        this.calfeed_PropertiesDmean()
    },     
    
    calfeed_PropertiesDmean() { //+
        let newvalue = Math.pow((this.$store.getters.secondDMCPropertiesTopSize*this.$store.getters.secondDMCPropertiesBottomSize), 0.5).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesDmean',
            value: newvalue
        });
    },

    calfeed_PropertiesRD50() { //+
        let newvalue = parseFloat(parseFloat(this.$store.getters.firstDMCMedium_m4density+0.125)+0.154*this.$store.getters.secondDMCPropertiesSGum-0.215*this.$store.getters.secondDMCPropertiesSGom).toFixed(3)
        this.$store.commit("setX", {
            x: 'secondDMCPropertiesRD50',
            value: newvalue
        })
    },    

  },
  computed: {
    ...mapGetters([
    ])
  },
  data() {
    return {
      dialog: false,
      e1: 1
    };
  }
};
</script>