<template>
  <v-layout row justify-center>
    <v-tooltip top>
      <template v-slot:activator="{ on }">
        <v-btn 
        depressed
        v-on="on"
        fab    
        x-large
        class="transparent"
        absolute    
        @click.native.stop="dialog = true"  
        ></v-btn> 
      </template>     
      <span style="color:orange">FIRST DMV</span>
    </v-tooltip>
    <v-dialog v-model="dialog" max-width="600px">
      <v-stepper v-model="e1">
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-12"> 
              <v-card-title> 1st DMV </v-card-title>
              <v-flex pa-5> 
                <v-flex>
                    <span style="color:orange">SGm: {{$store.getters.dmvM1Density}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:white">Top Size (Feed): </span>
                    <input v-model="$store.getters.firstDMVtopSize" @input="getTopSize" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:white">Bottom Size (Feed): </span>
                    <input v-model="$store.getters.firstDMVbottomSize" @input="getBottomSize" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:white">Feed ash:  </span>
                    <input v-model="$store.getters.firstDMVfeedAsh" @input="getFeedAsh" placeholder="doldur" />
                </v-flex>           
                <v-flex>
                    <span style="color:white">Feed Mass, t/h:  </span>
                    <input v-model="$store.getters.firstDMVfeedMassth" @input="getFeedMassTH" placeholder="doldur" />
                </v-flex>                         
                <v-flex>
                    <span style="color:white">Feed Mass, %:  </span>
                    <input v-model="$store.getters.firstDMVfeedMass" @input="getFeedMass" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:orange">Dmean: {{$store.getters.firstDMVdmean}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">RD50: {{$store.getters.dmvM1Density}} </span>
                </v-flex>              
                <v-flex>
                    <span style="color:orange">Clean coal %: {{$store.getters.firstDMVcleanCoal}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">Clean Coal, t/h: {{$store.getters.firstDMVcleanCoalth}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:white">Clean coal ash %: </span>
                    <input v-model="$store.getters.firstDMVcleancoalAsh" @input="getCleanCoalash" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:orange">Tailing %: {{$store.getters.firstDMVtailing}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">Tailing, t/h: {{$store.getters.firstDMVtailingth}} </span>
                </v-flex>              
                <v-flex>
                    <span style="color:white">Tailing ash %: </span>
                    <input v-model="$store.getters.firstDMVtailingAsh" @input="getTailingAsh" placeholder="doldur" />
                </v-flex>
              </v-flex>
            </v-card>
                <div class="text-center">
                    <v-dialog
                    width="500"
                    >
                    <template v-slot:activator="{ on }">
                        <v-btn v-on="on">
                        Medium Properties
                        </v-btn>
                    </template>
                    <v-card>
                        <v-card-title> Medium Properties </v-card-title>
                        <v-flex pa-6>
                            <v-flex>
                                <span style="color:white">M1 density: </span>
                                <input v-model="$store.getters.dmvM1Density" @input="getdmvM1Density" placeholder="doldur" />
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">M1 flow rate: </span>
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">M1 PSD:</span>
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">MCR1: </span>
                            </v-flex>
                            <v-flex>
                                <span style="color:white">M2 Density: </span>
                                <input v-model="$store.getters.dmvM2Density" @input="getdmvM2Density" placeholder="doldur" />
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">M2 flow rate:</span>
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">M2 PSD: </span>
                            </v-flex>
                            <v-flex>
                                <span style="color:orange">MCR2: </span>
                            </v-flex>  
                        </v-flex>                                                                                                                                                                                                  
                    </v-card>
                    </v-dialog>
                </div>            
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  methods: {    
        getdmvM1Density(event) {
            this.$store.commit("setX", {
                x: 'dmvM1Density',
                value: event.target.value
            })
        },

        getdmvM2Density(event) {
            this.$store.commit("setX", {
                x: 'dmvM2Density',
                value: event.target.value
            })
        },

        getTopSize(event) {
            this.$store.commit("setX", {
                x: 'firstDMVtopSize',
                value: event.target.value
            })
            this.calDmean()
        },

        getBottomSize(event) {
            this.$store.commit("setX", {
                x: 'firstDMVbottomSize',
                value: event.target.value
            })
            this.calDmean()
        },

        calDmean() {
            let newValue = this.$store.getters.firstDMVtopSize * this.$store.getters.firstDMVbottomSize
            let sonuc = Math.pow(newValue, 0.5).toFixed(3)
            this.$store.commit("setX", {
                x: 'firstDMVdmean',
                value: sonuc
            })
        },

        calCleanCoal() {
            let newValue = (this.$store.getters.firstDMVfeedMass * (this.$store.getters.firstDMVfeedAsh - this.$store.getters.firstDMVtailingAsh) / (this.$store.getters.firstDMVcleancoalAsh - this.$store.getters.firstDMVtailingAsh)).toFixed(3)
            this.$store.commit("setX", {
                x: 'firstDMVcleanCoal',
                value: newValue
            })
            this.calCleanCoalth()
            this.calTailing()
        },

        calCleanCoalth() {
            let newValue = (this.$store.getters.firstDMVfeedMassth * this.$store.getters.firstDMVcleanCoal / 100 ).toFixed(3)
            this.$store.commit("setX", {
                x: 'firstDMVcleanCoalth',
                value: newValue
            })
        },

        calTailing() {
            let newValue = (100 - this.$store.getters.firstDMVcleanCoal).toFixed(3)
            this.$store.commit("setX", {
                x: 'firstDMVtailing',
                value: newValue
            })
            this.calTailingth()
        },

        calTailingth() {
            let newValue = (this.$store.getters.firstDMVfeedMassth * this.$store.getters.firstDMVtailing / 100).toFixed(3)
            this.$store.commit("setX", {
                x: 'firstDMVtailingth',
                value: newValue
            })
        },

        getFeedAsh(event) {
            this.$store.commit("setX", {
                x: 'firstDMVfeedAsh',
                value: event.target.value
            })
            this.calCleanCoal()
        },

        getFeedMassTH(event) {
            this.$store.commit("setX", {
                x: 'firstDMVfeedMassth',
                value: event.target.value
            })
            this.calCleanCoalth()
            this.calTailingth()
        },

        getFeedMass(event) {
            this.$store.commit("setX", {
                x: 'firstDMVfeedMass',
                value: event.target.value
            })
            this.calCleanCoal()
        },

        getCleanCoalash(event) {
            this.$store.commit("setX", {
                x: 'firstDMVcleancoalAsh',
                value: event.target.value
            })
            this.calCleanCoal()
        },

        getTailingAsh(event) {
            this.$store.commit("setX", {
                x: 'firstDMVtailingAsh',
                value: event.target.value
            })
            this.calCleanCoal()
        }
  },
  computed: {
    ...mapGetters([
    ])
  },
  data() {
    return {
      dialog: false,
      e1: 1
    };
  }
};
</script>