<template>
  <v-layout row justify-center>
        <v-tooltip top>
      <template v-slot:activator="{ on }">
        <v-btn 
        depressed
        v-on="on"
        fab    
        x-large
        class="transparent"
        absolute    
        @click.native.stop="dialog = true"  
        ></v-btn> 
      </template>     
      <span style="color:orange">SECOND DMV</span>
    </v-tooltip>
    <v-dialog v-model="dialog" max-width="600px">
      <v-stepper v-model="e1">
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-12"> 
              <v-card-title> 2nd DMV </v-card-title>
              <v-flex pa-5> 
                <v-flex>
                    <span style="color:orange">SGm: {{$store.getters.dmvM2Density}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:white">Top Size (Feed): </span>
                    <input v-model="$store.getters.secondDMVtopSize" @input="getTopSize" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:white">Bottom Size (Feed): </span>
                    <input v-model="$store.getters.secondDMVbottomSize" @input="getBottomSize" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:orange">Feed ash: {{$store.getters.firstDMVtailingAsh}} </span>
                </v-flex>           
                <v-flex>
                    <span style="color:orange">Feed Mass, t/h: {{$store.getters.firstDMVtailingth}} </span>
                </v-flex>                         
                <v-flex>
                    <span style="color:orange">Feed Mass, %: {{$store.getters.firstDMVtailing}}  </span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">Dmean: {{$store.getters.secondDMVdmean}}</span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">RD50: {{$store.getters.dmvM2Density}} </span>
                </v-flex>              
                <v-flex>
                    <span style="color:orange">Middling %: {{$store.getters.secondDMVmiddling}}</span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">Middling, t/h: {{$store.getters.secondDMVmiddlingth}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:white">Middling ash %:  </span>
                    <input v-model="$store.getters.secondDMVmiddlingAsh" @input="getmidAsh" placeholder="doldur" />
                </v-flex>
                <v-flex>
                    <span style="color:orange">Tailing %: {{$store.getters.secondDMVtailing}} </span>
                </v-flex>
                <v-flex>
                    <span style="color:orange">Tailing, t/h: {{$store.getters.secondDMVtailingth}} </span>
                </v-flex>              
                <v-flex>
                    <span style="color:white">Tailing ash %: </span>
                    <input v-model="$store.getters.secondDMVtailingAsh" @input="getTailingAsh" placeholder="doldur" />
                </v-flex>
              </v-flex>
            </v-card>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>
  </v-layout>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  methods: {    
        getTopSize(event) {
            this.$store.commit("setX", {
                x: 'secondDMVtopSize',
                value: event.target.value
            })
            this.calDmean()
        },

        getBottomSize(event) {
            this.$store.commit("setX", {
                x: 'secondDMVbottomSize',
                value: event.target.value
            })
            this.calDmean()
        },

        calDmean() {
            let newValue = this.$store.getters.secondDMVtopSize * this.$store.getters.secondDMVbottomSize
            let sonuc = Math.pow(newValue, 0.5).toFixed(3)
            this.$store.commit("setX", {
                x: 'secondDMVdmean',
                value: sonuc
            })
        },  
        
        getTailingAsh(event) {
            this.$store.commit("setX", {
                x: 'secondDMVtailingAsh',
                value: event.target.value
            })
            this.calmid()
        },
        
        getmidAsh(event) {
            this.$store.commit("setX", {
                x: 'secondDMVmiddlingAsh',
                value: event.target.value
            })
            this.calmid()
        },     
        
        calmid() {
          let newValue = (this.$store.getters.firstDMVtailing * ((this.$store.getters.firstDMVtailingAsh - this.$store.getters.secondDMVtailingAsh) / (this.$store.getters.secondDMVmiddlingAsh -  this.$store.getters.secondDMVtailingAsh)) * this.$store.getters.firstDMVtailingAsh / 100).toFixed(3)
          this.$store.commit("setX", {
            x: 'secondDMVmiddling',
            value: newValue
          })
          this.calmidth()
          this.caltailing()
        },

        calmidth() {
          let newValue = (this.$store.getters.firstDMVfeedMassth * this.$store.getters.secondDMVmiddling / 100).toFixed(3)
          this.$store.commit("setX", {
            x: 'secondDMVmiddlingth',
            value: newValue
          })
          this.calTailingth()
        },

        caltailing() {
          let newValue = this.$store.getters.firstDMVtailingAsh - this.$store.getters.secondDMVmiddling 
          this.$store.commit("setX", {
            x: 'secondDMVtailing',
            value: newValue
          })
        },

        calTailingth() {
          let newValue = this.$store.getters.firstDMVtailingth - this.$store.getters.secondDMVmiddlingth
          this.$store.commit("setX", {
            x: 'secondDMVtailingth',
            value: newValue
          })
        }

  },
  computed: {
    ...mapGetters([
    ])
  },
  data() {
    return {
      dialog: false
    };
  }
};
</script>